@import "variables";

body {
  font-family: $Font_family;
  margin: 0%; }


.foot {
  background-color: $Secondary_color;
  padding-bottom: 50px; }


.text {
  color: $Text_color; }


.net-icon {
  margin-left: 10px;
  margin-top: 16px; }

.me {
  display: flex;
  color: white;
  margin-left: 10px; }


#cover {
  width: 100%; }


.why {
  background-color: $Secondary_color;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 10px; }


.links {
  list-style-type: none; }


.Header {
  margin-left: 37px; }


.image {
  position: absolute;
  right: 1%; }


.link {
  color: $link_color;
  border-bottom: 2px solid transparent;
  text-decoration: none !important;
  transition: border-bottom 0.5s, color 0.5s; }


.link:hover {
  border-bottom: 2px solid $link_hovered;
  color: $link_hovered; }


.main-body {
  margin-left: 10%;
  margin-bottom: 10px;
  margin-top: 60px;
  width: clamp(45ch, 100%, 130ch); }


.list-item {
  margin: 0%;
  line-height: 25px;
  color: $Text_color;
  text-decoration: none !important; }


.list-item:hover {
  color: white;
  text-decoration: underline; }


.list-important {
  line-height: 25px; }


.code {
  background-color: $code_background;
  color: $Primary_color;
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 8px; }


.share {
  border: 1px solid $Main_border_color;
  border-radius: 5px;
  padding-left: 5%; }


table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%; }


th {
  border: 1px solid $Main_border_color;
  text-align: center;
  padding: 8px; }


td {
  border: 1px solid $Main_border_color;
  text-align: left;
  padding: 8px; }


tr:nth-child(even) {
  color: $Primary_color;
  background-color: $Main_border_color; }


.keyword {
  color: $Secondary_color; }


/* About Us page */
.container {
  margin-left: 5%;
  margin-right: 5%; }


.welcome {
  background-image: url("../../assets/logo/profile.png");
  height: 500px;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain; }


.welcome > .welcome-wrapper {
  position: absolute;
  top: 203px; }


.about-us,
.mission,
.future-idea {
  display: flex;
  justify-content: space-between; }


.ezpie-info .header {
  border-bottom: 4px solid black;
  width: fit-content; }


.about-text,
.mission-text,
.future-text {
  width: 60%; }

/* ---------------------------------------- */

/* Navbar Style */
.navbar {
  background-color: $Secondary_color;
  position: fixed;
  top: 0;
  width: 100%;
  text-align: center;
  /* z-index: 1 */
  margin-bottom: 100px; }


.nav-link {
  color: #f2f2f2;
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  text-decoration: none;
  font-size: 25px;
  transition: color 0.5s, text-decoration 0.5s; }


.nav-link:hover {
  color: $link_hovered;
  text-decoration: underline; }


/* NOTE This is for about us page */
@media screen and (max-width: 1024px) {
  .about,
  .mission,
  .future-idea {
    display: block; } }



/* NOTE this is for tabs */
@media screen and (max-width: 1055px) {
  .main-body {
    margin-right: 204px; }

  /* -------------- Footer ------------------ */
  .foot-main {
    padding-top: 100px;
    color: black;
    margin-bottom: 60px; }


  .detail {
    text-align: center;
    border-bottom: 1px solid white; }


  .Header {
    margin-left: 40px; }


  .tags,
  .challenges,
  .Company {
    text-align: center; }


  .info {
    display: flex;
    position: absolute;
    left: 10%; } }



@media screen and (max-width: 532px) {
  .info {
    display: block;
    position: relative;
    text-align: center; } }



/* NOTE this is for laptops */

@media screen and (min-width: 1055px) {
  .net {
    margin-left: 200px; }


  .net-icon {
    text-decoration: none;
    margin-left: 10px; }


  /* -------------- footer ----------------- */
  .foot-main {
    padding-top: 100px;
    display: flex;
    padding-left: 180px;
    margin-bottom: 60px; }


  .tags {
    margin-left: 200px; }


  .challenges {
    margin-left: 20px; }


  /* ------------------------------------------- */

  .why-wrapper {
    margin-top: 50px;
    padding-left: 140px;
    padding-right: 140px;
    display: flex; }


  .after {
    margin-left: 170px; }

  #main {
    margin-bottom: 100px; }


  .info {
    display: flex;
    position: absolute;
    left: 10%; } }


